"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { MentorSidebar } from "@/components/mentor-sidebar"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Loader2, Users } from "lucide-react"

export default function AllStudentsPage() {
  const supabase = createClient()
  const [students, setStudents] = useState<any[]>([])
  const [assignedIds, setAssignedIds] = useState<string[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadStudents = async () => {
      try {
        setLoading(true)

        // 1. Load mentor ID
        const { data: { user } } = await supabase.auth.getUser()
        if (!user?.id) return
        const mentorId = user.id

        // 2. Fetch all students
        const { data: studentList, error: studentsErr } = await supabase
          .from("profiles")
          .select("id, full_name, avatar_url, role")
          .eq("role", "student")
          .order("full_name", { ascending: true })

        if (studentsErr) throw studentsErr

        setStudents(studentList || [])

        // 3. Fetch assigned students
        const { data: assigned } = await supabase
          .from("student_mentor_assignments")
          .select("student_id")
          .eq("mentor_id", mentorId)
          .eq("is_active", true)

        setAssignedIds(assigned?.map(a => a.student_id) || [])

      } catch (error) {
        console.error("Error loading student list:", error)
      } finally {
        setLoading(false)
      }
    }

    loadStudents()
  }, [])

  return (
    <div className="flex h-screen bg-background">
      <MentorSidebar />

      <div className="flex-1 overflow-auto">
        <div className="container mx-auto p-6">

          {/* HEADER */}
          <div className="mb-8">
            <h1 className="text-3xl font-bold flex items-center gap-2">
              <Users className="w-8 h-8" />
              All Students
            </h1>
            <p className="text-muted-foreground mt-2">
              View all students enrolled in the Gamified IAS Academy
            </p>
          </div>

          {/* LOADING STATE */}
          {loading ? (
            <div className="flex justify-center items-center h-64">
              <Loader2 className="w-8 h-8 animate-spin" />
            </div>
          ) : (
            <div className="grid md:grid-cols-3 gap-6">
              {students.map((student) => {
                const assigned = assignedIds.includes(student.id)

                return (
                  <Card key={student.id} className="shadow-sm hover:shadow-md transition">
                    <CardContent className="p-6">

                      {/* Avatar + Name */}
                      <div className="flex items-center gap-4">
                        <img
                          src={student.avatar_url || "/default.png"}
                          alt={student.full_name}
                          className="w-14 h-14 rounded-full object-cover"
                        />

                        <div>
                          <h3 className="font-semibold">{student.full_name}</h3>
                          <p className="text-xs text-muted-foreground">Student</p>

                          {/* ASSIGNED BADGE */}
                          {assigned && (
                            <Badge className="bg-green-600 mt-1">Assigned to You</Badge>
                          )}
                        </div>
                      </div>

                    </CardContent>
                  </Card>
                )
              })}
            </div>
          )}

        </div>
      </div>
    </div>
  )
}
